<template>
  <div class="result">
    <mt-header fixed :title.sync="name">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="results">
      <goods v-for="item in results" :goods="item" :key="item.id" @click.native="toRecord(item)"></goods>
    </div>
  </div>
</template>

<script>
import TabBar from '@/components/TabBar';
import Goods from '@/components/Goods';
import { GoodsAPI } from '@/api';

export default {
  components: {
    TabBar,
    Goods,
  },
  data() {
    return {
      name: null,
      results: [],
    };
  },
  created() {
    const { name } = this.$route.params;
    if (name) {
      this.name = name;
      this.getData();
    }
  },
  methods: {
    getData() {
      GoodsAPI.findByName({ name: this.name }).then((response) => {
        this.results = response.data;
      });
    },
    toRecord(item) {
      this.$router.push({ name: 'record', params: { id: item.id } });
    },
  },
  watch: {
    $route(to) {
      const { name } = to.params;
      if (name) {
        this.name = name;
        this.getData();
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../styles/index.scss';

  .results {
      margin-top: 52px;
  }
</style>



