<template>
  <div class="category">
    <div v-for="item in datas">
      <span>{{item.name}}</span>
    </div>
    <tab-bar select="goods"></tab-bar>
  </div>
</template>

<script>
import TabBar from '@/components/TabBar';
import { RecordAPI } from '@/api';

export default {
  components: {
    TabBar,
  },
  data() {
    return {
      datas: [],
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      RecordAPI.category({ supplierId: 1 }).then((response) => {
        this.datas = response.data;
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../styles/index.scss';

.category {

}
</style>



