<template>
  <div class="dataverify">
    <mt-header fixed title="完善店铺信息" class="head">
      <router-link to="/" slot="left">
        <icon name="fanhui" scale="1.4" class="register-iconn"></icon>
      </router-link>
    </mt-header>
    <div class="tiao">完善店铺信息后即可下单进货</div>
    <mt-field type="text" label="店铺名称" class="fieldline" placeholder="2-20个汉字" :attr="{maxlength:20,minlength:2}"></mt-field>
    <mt-field label="收获联系人" placeholder="货某某" slot='right' class="fieldline">
      <div class="sex">
        <label for="man">
          <input type="radio" name="sex" value="先生" id='man' checked="checked">
          <icon name='nan' class="iconn sexicon" scale="1" style="transform:translate(-20%) rotate(-90deg)"></icon>
          <span>先生</span>
        </label>
        <label for="women">
          <input type="radio" name="sex" value="女士" id="women">
          <icon name="nv" class="iconn sexicon" scale="1.15" style="transform:rotate(180deg);"></icon>
          <span>女士</span>
        </label>
      </div>
    </mt-field>
    <mt-field type="tel" label="联系电话" placeholder="15910696727" slot="right" class="fieldline">
      <div class="connect" @click="canedit()">
        <icon name='bianji' scale='1.4' class="iconn"></icon>
      </div>
    </mt-field>
    <mt-field type="text" label="验证码" placeholder="请输入验证码" class="authcode fieldline" v-show="editflag" slot="right" :attr="{maxlength:4,minlength:4}">
      <mt-button class="getagain">再次获取</mt-button>
    </mt-field>
    <mt-field label="收货地址" placeholder="北京市朝阳区，北辰新纪元" class="fieldline"></mt-field>
    <div class="update">
      <div class="update-title">
        <span class="title">店铺门头照片</span>
        <span class="explain">(请上传完整的店铺门头照片)</span>
      </div>
      <div class="con">
        <div class="mentou-images"></div>
        <input type="file" id='img'>
      </div>
    </div>
    <div class="update" style="margin-bottom:1.28rem;">
      <div class="update-title">
        <span class="title">营业执照照片</span>
        <span class="explain">(请上传真实且清晰的营业执照照片)</span>
      </div>
      <div class="con">
        <div class="zhizhao-images"></div>
        <input type="file" id='img'>
      </div>
    </div>
    <mt-button type="primary" class="toupdate">上传</mt-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      editflag: false,
    };
  },
  methods: {
    canedit() {
      this.editflag = true;
    },
  },
};
</script>

<style lang='scss'>
@import '../styles/index.scss';
.dataverify {
  .fieldline {
    border-bottom: 1px solid $line-color;
  }
  .authcode {
    background: $line-color;
    input {
      background: $line-color;
    }
  }
  .getagain {
    @include nextStep($main-color, #fff, 2.04rem);
    height: 0.54rem;
    line-height: 0.54rem;
    font-size: 10px;
    -webkit-transform: translateY(-23%);
  }
  .sex {
    width: 2.5rem;
    height: 0.94rem;
    border-left: 1px solid $none-color;
    display: flex;
    @extend .vh-center;
    input~* {
      color: $none-color;
    }
    input:checked~* {
      color: $main-color;
      font-weight: 500;
    }
    label {
      width: 50%;
      text-align: right;
      display: flex;
      line-height: 0.94rem;
      font-size: 0.3rem;
      font-weight: 500;
      span {
        display: inline-block;
        width: 0.7rem;
      }
      @extend .vh-center;
      input {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
      }
    }
  }
  .connect {
    color: $main-color;
  }
  .update {
    margin: 0 auto;
    border-bottom: 1px solid $line-color;
    padding: 0.36rem 0.24rem;
    line-height: 0;
    box-sizing: border-box;
    .update-title {
      height: auto;
      .title {
        font-size: 0.32rem;
        line-height: 0.32rem;
      }
      .explain {
        color: $none-color;
        font-size: 0.2rem;
      }
    }
    .con {
      position: relative;
      height: 3.8rem;
      margin-top: 0.36rem;
      input {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        z-index: 99;
      }
      .mentou-images {
        width: 100%;
        height: 100%;
        background: url(../assets/images/hqq_dianpumentou@3x.png) no-repeat center center/contain;
        position: absolute;
        left: 0;
        top: 0;
      }
      .zhizhao-images {
        width: 100%;
        height: 100%;
        background: url(../assets/images/hqq_yingyezhizhaodianjishangchuan@3x.png) no-repeat center center/contain;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
  .toupdate {
    @include nextStep($main-color);
    letter-spacing: 2px;
    position: fixed;
    bottom: 0.36rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
    z-index:999;
  }
}
</style>




