import Moment from 'moment';

const cny = (val) => {
  const n = Number.parseFloat(val);
  return '￥'.concat(n.toFixed(2));
};


const price = (val) => {
  const n = Number.parseFloat(val);
  return n.toFixed(2);
};

const img = val => `http://img-a.hqq.vip/${val}`;

const datetime = (val) => {
  const time = Moment(val);
  return time.format('YYYY-MM-DD HH:mm:ss');
};

const dateMonth = (val) => {
  const time = Moment(val);
  return time.format('MM-DD');
};

const date = (val) => {
  const time = Moment(val);
  return time.format('YYYY-MM-DD');
};

export default {
  cny, price, img, datetime, date, dateMonth,
};



