<template>
  <div class="search">
    <div class="search-header">
      <mt-search v-model="value" cancel-text="" placeholder="输入商品名称入库" :result="result" @input="input" autocomplete="off">
        <router-link v-for="name in result" :to="{ name: 'result', params: { name } }" :key="name">
          <span class="suggest-item">{{name}}</span>
        </router-link>
      </mt-search>
      <div class="search-right">
        <div class="icon">
          <icon name="lishi"></icon>
        </div>
        <div>
          <span>历史</span>
        </div>
      </div>
    </div>
    <router-link to="/login">
      <div class="nologin">
        <span>登录后统计进货</span>
        <icon name="right"></icon>
      </div>
    </router-link>
    <tab-bar select="scan"></tab-bar>
  </div>
</template>

<script>
import _ from 'lodash';
import TabBar from '@/components/TabBar';
import { GoodsAPI } from '@/api';

export default {
  components: {
    TabBar,
  },
  data() {
    return {
      value: null,
      result: [],
    };
  },
  methods: {
    input: _.debounce(function (val) { // eslint-disable-line func-names
      if (!val) {
        return;
      }
      GoodsAPI.autocomplete({ name: val }).then((response) => {
        this.result = response.data;
      });
    }, 500),
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../styles/index.scss';

.search {
  .nologin {
    background-color: $light-theme-color;
    padding: $gutter;
    color: $theme-color;
    text-align: center;
    svg {
      float: right;
      padding-top: 4px;
    }
  }
  .search-header {
    display: flex;
    height: 52px;
    .mint-search {
      flex: 5;
      .mint-search-list {
        padding-top: 52px;
        padding-bottom: 55px;
        .mint-search-list-warp {
          display: flex;
          flex-direction: column;
          &>a {
            color: #666;
            background-color: #fff;
            padding: $gutter/2 $gutter; // .suggest-item {
            //   padding: $gutter;
            // }
          }
          &>a:hover {
            background-color: #eee;
          }
        }
      }
    }
    .search-right {
      position: relative;
      top: 0;
      right: 0;
      flex: 1 1 0%;
      background-color: $dark-color;
      display: flex;
      align-items: center;
      color: #fff;
      .icon {
        padding-top: 6px;
        padding-right: 6px;
      }
    }
    .mint-searchbar {
      flex: 5;
      background-color: $dark-color;
      .mint-searchbar-inner {
        flex: 5 1 0%;
        border-top-left-radius: 24px;
        border-bottom-left-radius: 24px;
        border-top-right-radius: 24px;
        border-bottom-right-radius: 24px;
        padding: 4px 8px;
        min-height: 28px;
        .mintui-search {
          font-size: 18px;
          padding: 4px 6px;
        }
      }
    }
  }
}
</style>



