<template>
  <div class="record-table">
    <table>
      <colgroup class="table-col" align="center" v-for="(item, key) in header" :key="key"></colgroup>
      <colgroup class="table-col" align="center"></colgroup>
      <thead>
        <tr>
          <th v-for="(item, key) in header" :key="key" :width="item.width">{{item.title}}</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in data" :key="key">
          <td v-for="(vals, index) in header" :key="index" v-text="renderCell(item, vals)">{{item[vals.name]}}</td>
          <td>
            <div class="right-icon">
              <icon name="right"></icon>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import Vue from 'vue';

export default {
  props: {
    header: {
      type: Array,
      default: () => [
        {
          name: 'stockDate',
          title: '日期',
          filter: 'dateMonth',
          width: 60,
        },
        {
          name: 'amount',
          title: '进货数量',
          width: 80,
        },
        {
          name: 'price',
          title: '单价',
          filter: 'cny',
        },
        {
          name: 'discountPrice',
          title: '优惠后单价',
          filter: 'cny',
          width: 100,
        },
        {
          name: 'total',
          title: '总金额',
          filter: 'cny',
        },
      ],
    },
    data: Array,
  },
  methods: {
    renderCell(row, head) {
      const val = row[head.name];
      if (head.filter) {
        const filter = Vue.filter(head.filter);
        return filter(val);
      }
      if (head.fmt) {
        return head.fmt(val);
      }
      return val;
    },
  },
  data() {
    return {
      currentValue: null,
    };
  },
};
</script>
<style lang="scss">
@import '../styles/index.scss';
.record-table {
  table {
    .table-col {
      text-align: left;
      padding-left: .5rem;
      padding-right: .5rem;
    }
    border-spacing: 0;
    width: 100%;
    thead {
      background-color: #e9e5da;
    }
    th, td {
      height: 48px;
      padding: 0 .1rem;
      vertical-align: middle;
      text-align: center;
      max-width: 100px;
      // @include ellipsisLn(2);
      // // overflow: hidden;
      // // text-overflow: ellipsis;
      // // white-space: nowrap;
    }
    tbody {
      padding-left: .5rem;
      padding-right: .5rem;
    }
    tbody tr {
      td {
        text-align: center;
        &:first-child {
          padding-left: .5rem;
        }
        &:last-child {
          padding-right: .5rem;
        }
      }
      background-color: #fff;
      &:nth-child(2n+1) {
        background-color: #f9f9f9;
      }
    }
    td .right-icon {
      @extend .h-center;
      color: #ddd;
    }
  }
}
</style>




