const wx = require('weixin-js-sdk');

const wechat = {};

wechat.install = (Vue) => {
  Vue.prototype.$wechat = wx; // eslint-disable-line no-param-reassign
  Vue.wechat = wx; // eslint-disable-line no-param-reassign
};

export default wechat;



