<template>
  <div class="records journal">
    <mt-header fixed title="流水">
      <router-link to="/" slot="left">
        <icon name="shouye" scale="1.25"></icon>
      </router-link>
    </mt-header>
    <div class="records-dates">
      <span class="date-item" :class="{'active': duration === 7}" @click="selectDays(7)">1 周</span>
      <span class="date-item" :class="{'active': duration === 1}" @click="selectDays(1, 'months')">1 个月</span>
      <span class="date-item" :class="{'active': duration === 3}" @click="selectDays(3, 'months')">3 个月</span>
      <span class="date-item" :class="{'active': duration < 0}" @click="selectDays(-1)">日期筛选</span>
    </div>
    <div class="records-date-select" v-show="duration < 0">
      <div class="fields">
        <div @click="openPicker('pickerStart')">
          <span>{{startDate | date}}</span>
          <icon name="rili"></icon>
        </div>
        <span>至</span>
        <div @click="openPicker('pickerEnd')">
          <span>{{endDate | date}}</span>
          <icon name="rili"></icon>
        </div>
      </div>
      <div class="select-btn">
        <mt-button size="small" @click.native="getDatas">确认</mt-button>
      </div>
    </div>
    <div class="total-money">
      <p>进货金额（元）</p>
      <p class="money">{{totalMoney | price}}</p>
    </div>
    <record-table :header="header" :data="records"></record-table>
    <mt-datetime-picker ref="pickerStart" type="date" v-model="startDate">
    </mt-datetime-picker>
    <mt-datetime-picker ref="pickerEnd" type="date" v-model="endDate">
    </mt-datetime-picker>
  </div>
</template>

<script>
// import { MessageBox, Toast, Indicator } from 'mint-ui';
import moment from 'moment';
import { RecordAPI } from '@/api';
import RecordTable from '@/components/RecordTable';

export default {
  components: {
    RecordTable,
  },
  data() {
    return {
      goods: {
        id: null,
      },
      records: [],
      pager: {
        page: 0,
        size: 5,
        direction: 'desc',
        sort: 'stockDate',
      },
      startDate: null,
      endDate: new Date(),
      duration: 7,
      unit: 'days',
      totalMoney: 0,
      header: [
        {
          name: 'stockDate',
          title: '日期',
          filter: 'dateMonth',
          width: 60,
        },
        {
          name: 'name',
          title: '名称',
          width: 110,
        },
        {
          name: 'spec',
          title: '规格',
        },
        {
          name: 'amount',
          title: '进货量',
          width: 60,
        },
        {
          name: 'total',
          title: '总金额',
          filter: 'cny',
        },
      ],
    };
  },
  created() {
    this.startDate = moment(this.endDate).subtract(this.duration, this.unit).toDate();
    this.getData();
    this.getDatas();
  },
  computed: {
    discountPrice() {
      let total = this.record.price * this.record.amount;
      total -= this.record.discount;
      const fee = Number(this.record.amount) + Number(this.record.quantity);
      if (fee === 0) {
        return 0;
      }
      return total / fee;
    },
    total() {
      return this.record.price * this.record.amount;
    },
  },
  methods: {
    openPicker(ref) {
      this.$refs[ref].open();
    },
    selectDays(days, unit) {
      this.duration = days || 7;
      this.unit = unit || 'days';
    },
    getData() {
      const params = {
        startDate: moment(this.startDate).format('YYYY-MM-DD HH:mm:ss'),
        endDate: moment(this.endDate).format('YYYY-MM-DD HH:mm:ss'),
      };
      RecordAPI.total(params).then((response) => {
        this.totalMoney = response.data || 0;
      });
    },
    getDatas() {
      const params = {
        goodsNo: this.goods.goodsNo,
        ...this.pager,
      };
      RecordAPI.all(params).then((response) => {
        this.records = response.data.content;
      });
    },
  },
  watch: {
    duration(val) {
      const now = new Date();
      if (val > 0) {
        this.startDate = moment(now).subtract(val, this.unit).toDate();
        this.endDate = now;
        this.getData();
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../styles/index.scss';

$bg-color: #354151;
$dark-bg-color: #232c3b;
$light-bg-color: #b6bbc1;
$journal-font-color: #7d8590;

.records.journal {
  .records-dates {
    background-color: #354151;
    .date-item {
      background-color: $light-bg-color;
      color: #656c76;
      border: 1px solid $light-bg-color;
      &.active {
        background-color: $active-theme-color;
        border: 1px solid $active-theme-color;
        color: #fff;
      }
    }
  }
  .records-date-select {
    background-color: $dark-bg-color;
    .fields {
      color: $journal-font-color;
      div:first-child,
      div:last-child {
        background-color: $bg-color;
        color: $journal-font-color;
      }
    }
    .select-btn {
      .mint-button--small {
        height: 31px;
      }
    }
  }
  .total-money {
    background-color: $bg-color;
    color: #656c76;
    padding: $gutter;
    .money {
      color: #fff;
      font-size: 3rem;
    }
  }
}
</style>



