<template>
  <div>
    <mt-tabbar v-model="selected">
      <mt-tab-item id="外卖" v-for="item in tabs" :key="item.name" :class="{'active': selected === item.name}" @click.native="goTo(item)">
        <icon :name="item.icon" slot="icon"></icon>
        {{item.title}}
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>

export default {
  name: 'hello',
  props: {
    select: {
      type: String,
      default: 'scan',
    },
  },
  data() {
    return {
      selected: this.select,
      tabs: [
        {
          name: 'goods',
          title: '商品',
          url: '/goods',
          icon: 'shangpin',
        },
        {
          name: 'scan',
          title: '扫码入库',
          url: '/goods',
          icon: 'saomaruku',
        },
        {
          name: 'journals',
          title: '流水',
          url: '/journals',
          icon: 'liushui',
        },
      ],
    };
  },
  methods: {
    goTo(item) {
      this.selected = item.name;
      if (item.name === 'scan') {
        const that = this;
        this.$wechat.scanQRCode({
          needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
          scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
          success: (res) => {
            const result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
            that.$router.push({ name: 'search', query: { barcode: result } });
          },
        });
        return;
      }
      this.$router.push({ path: item.url });
    },
  },
};
</script>

