<template>
  <div class="records record">
    <mt-header fixed title="录入价格 / 记账">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="record-spec">
      <span>规格：{{goods.spec}} / {{goods.units}}</span>
    </div>
    <div class="record-goods">
      <div class="record-goods-avatar">
        <img :src="goods.thumbnail | img" :alt="goods.name">
      </div>
      <div class="record-goods-info">
        <div>
          <span class="record-goods-info-name">品&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;牌：</span>
          <span class="record-goods-info-value">{{goods.brand}}</span>
        </div>
        <div>
          <span class="record-goods-info-name">一级分类：</span>
          <span class="record-goods-info-value"></span>
        </div>
        <div>
          <span class="record-goods-info-name">二级分类：</span>
          <span class="record-goods-info-value"></span>
        </div>
        <div>
          <span class="record-goods-info-name">条&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;码：</span>
          <span class="record-goods-info-value">{{goods.barcode}}</span>
        </div>
      </div>
    </div>
    <div class="record-goods-name">
      <span>{{goods.name}}</span>
    </div>
    <div class="records-title">
      <div class="icon">
        <icon name="jilu" scale="1.25"></icon>
      </div>
      <div>
        <span>进货记录</span>
      </div>
    </div>
    <div class="records-dates">
      <span class="date-item" :class="{'active': duration === 7}" @click="selectDays(7)">1 周</span>
      <span class="date-item" :class="{'active': duration === 1}" @click="selectDays(1, 'months')">1 个月</span>
      <span class="date-item" :class="{'active': duration === 3}" @click="selectDays(3, 'months')">3 个月</span>
      <span class="date-item" :class="{'active': duration < 0}" @click="selectDays(-1)">日期筛选</span>
    </div>
    <div class="records-date-select" v-show="duration < 0">
      <div class="fields">
        <div @click="openPicker('pickerStart')">
          <span>{{startDate | date}}</span>
          <icon name="rili"></icon>
        </div>
        <span>至</span>
        <div @click="openPicker('pickerEnd')">
          <span>{{endDate | date}}</span>
          <icon name="rili"></icon>
        </div>
      </div>
      <div class="select-btn">
        <mt-button size="small">确认</mt-button>
      </div>
    </div>
    <record-table :data="records"></record-table>
    <mt-datetime-picker ref="pickerStart" type="date" v-model="startDate">
    </mt-datetime-picker>
    <mt-datetime-picker ref="pickerEnd" type="date" v-model="endDate">
    </mt-datetime-picker>
  </div>
</template>

<script>
// import { MessageBox, Toast, Indicator } from 'mint-ui';
import moment from 'moment';
import { GoodsAPI, RecordAPI } from '@/api';
import RecordTable from '@/components/RecordTable';

export default {
  components: {
    RecordTable,
  },
  data() {
    return {
      goods: {
        id: null,
      },
      records: [],
      pager: {
        page: 0,
        size: 5,
        direction: 'desc',
        sort: 'stockDate',
      },
      startDate: null,
      endDate: null,
      duration: 7,
      unit: 'days',
    };
  },
  mounted() {
    const { id } = this.$route.params;
    this.goods.id = id;
    this.getData();
  },
  computed: {
    discountPrice() {
      let total = this.record.price * this.record.amount;
      total -= this.record.discount;
      const fee = Number(this.record.amount) + Number(this.record.quantity);
      if (fee === 0) {
        return 0;
      }
      return total / fee;
    },
    total() {
      return this.record.price * this.record.amount;
    },
  },
  methods: {
    openPicker(ref) {
      this.$refs[ref].open();
    },
    selectDays(days, unit) {
      this.duration = days || 7;
      this.unit = unit || 'days';
    },
    getData() {
      GoodsAPI.findById(this.goods.id).then((response) => {
        this.goods = response.data;
        this.getDatas();
      });
    },
    getDatas() {
      const params = {
        goodsNo: this.goods.goodsNo,
        ...this.pager,
      };
      RecordAPI.all(params).then((response) => {
        this.records = response.data.content;
      });
    },
  },
  watch: {
    duration(val) {
      const now = new Date();
      if (val > 0) {
        this.startDate = now;
        this.endDate = moment(now).add(val, this.unit).toDate();
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../styles/index.scss';

.records {
  margin-top: $mt-header-height;
  .record-spec {
    @extend .v-center;
    padding: $gutter/2;
    background-color: $active-theme-color;
    color: #fff;
  }
  .record-goods {
    background-color: #fff;
    padding: $gutter/2 $gutter;
    display: flex;
    .record-goods-avatar {
      margin-right: $gutter;
      img {
        max-width: 6rem;
        border: 1px solid $grey-color;
        border-radius: 6px;
      }
    }
    .record-goods-info {
      .record-goods-info-name {
        color: #999;
      }
    }
  }
  .records-title {
    padding: $gutter/2 $gutter;
    @extend .v-center;
    .icon {
      padding-right: 4px;
    }
  }
  .records-dates {
    background-color: #fff;
    padding: $gutter;
    @extend .h-center;
    justify-content: space-around;
    .date-item {
      padding: 0.175rem 0.5rem;
      color: $font-color;
      background-color: #fff;
      border: 1px solid $border-color;
      border-radius: 4px;
      &.active {
        background-color: $theme-color;
        border: 1px solid $theme-color;
        color: #fff;
      }
    }
  }
  .records-date-select {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: $gutter;
    .fields {
      width: 80%;
      @extend .h-center;
      justify-content: space-around;
      div:first-child, div:last-child {
        padding: .25rem .5rem;
        border-radius: 4px;
        color: $font-color;
        background-color: #fff;
        @extend .h-center;
        & > span {
          padding-right: 6px;
        }
      }
    }
  }
}
</style>



