<template>
  <div class="login">
    <div class="logo">
      <img src="../assets/logo.png" alt="">
    </div>
    <mt-header fixed title="登录" class="head">
      <router-link to="/" slot="left">
        <icon name="fanhui" scale="1.4" class="iconn"></icon>
      </router-link>
    </mt-header>
    <div class="register-box">
      <span class="left">手机号</span>
      <input type="tel" placeholder="请输入手机号码" v-model="logintel.telephone">
      <div class="icon right" @click="dellogintel()">
        <icon name="guanbi" class="iconn" scale='1.3'></icon>
      </div>
    </div>
    <router-link to="">
      <mt-button type="primary" class="loginnextStep" @click='next()'>下一步</mt-button>
    </router-link>
    <router-link to='/register'>
       <span class='toregister'>新用户注册</span>
    </router-link>
  </div>
</template>
<script>
export default {
  data() {
    return {
      logintel: {
        telephone: '',
      },
    };
  },
  methods: {
    dellogintel() {
      this.logintel.telephone = '';
    },
    next() {
      this.$toast({
        message: '号码有误，请重新输入',
        iconClass: 'hintmessage',
      });
    },
  },
};
</script>

<style lang='scss'>
@import '../styles/index.scss';
.login{
  .logo{
    margin-top:2rem;
    display: flex;
    @extend .vh-center;
    img{
     width:2rem;
    }
  }
  .register-box {
    margin-top: 1.2rem;
    height: 0.8rem;
    text-align: left;
    border-bottom: 1px solid $line-color;
    display: flex;
    background: #fff;
    font-size:0.3rem;
    color:$none-color;
    .left {
      width: 20%;
      height: 100%;
      flex-grow: 0;
      text-indent:0.33rem;
      line-height: 0.8rem;
      color: $font-color;
      font-weight: bold;
    }
    input {
      flex-grow: 1;
      border: none;
      outline: none;
      color:$font-color;
      letter-spacing: 1px;
    }
    .right {
      width: 1.08rem;
      height: 0.8rem;
      display: flex;
      @extend .vh-center;
    }
  }
  .loginnextStep{
    @include nextStep($main-color);
  }
  .toregister{
    display: block;
    margin:0 auto;
    text-decoration:underline;
    color:$main-color;
    font-size:0.24rem;
    padding:0.3rem 0;
    text-align: center;
  }
}
</style>



