<template>
  <div class="registersuccess">
    <mt-header fixed title="注册成功" class="head">
      <router-link to="/" slot="left">
        <icon name="fanhui" scale="1.4" class="iconn"></icon>
      </router-link>
    </mt-header>
    <div class="success">
      <router-link to="/">
        <mt-button type='primary' class='bttn'>立即进入</mt-button>
      </router-link>
      <div class="mess">
        <span>{{time}}</span>后进入首页
      </div>
    </div>
  </div>
</template>
<script>

export default {
  data() {
    return {
      time: 3,
      interval: null,
    };
  },
  created() {
    this.interval = setInterval(() => {
      if (this.time <= 0) {
        clearInterval(this.interval);
        this.$router.push({ name: 'index' });
        return;
      }
      this.time -= 1;
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
};
</script>

<style lang='scss'>
@import '../styles/index.scss';
.registersuccess {
  .success {
    position: fixed;
    width: 100%;
    height: 100%;
    background: #fff url('../assets/images/registersuccess.png') no-repeat center 30%/40%;
    .bttn {
      @include nextStep(#fff,
      $main-color,
      45%);
      margin-top: 86%;
      border: 1px solid $main-color;
      letter-spacing: 3px;
    }
    .mess {
      margin-top: 0.43rem;
      text-align: center;
      font-size: 0.3rem;
      color: #C0C0C0;
      span {
        color: $main-color;
      }
    }
  }
}
</style>




