import Vue from 'vue';
import Router from 'vue-router';
import { AuthAPI } from '@/api';
import Index from '@/views/Index';
import Category from '@/views/Category';
import Result from '@/views/Result';
import Record from '@/views/Record';
import Records from '@/views/Records';
import Journals from '@/views/Journals';
import Login from '@/views/Login';
import Register from '@/views/Register';
import RegisterVerify from '@/views/RegisterVerify';
import PerfectPosition from '@/views/PerfectPosition';
import RegisterSuccess from '@/views/RegisterSuccess';
import DataVerify from '@/views/DataVerify';

Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
    },
    {
      path: '/goods',
      name: 'goods',
      component: Category,
    },
    {
      path: '/',
      name: 'scan',
      component: Index,
    },
    {
      path: '/result/:name',
      name: 'result',
      component: Result,
    },
    {
      path: '/record/:id',
      name: 'record',
      component: Record,
    },
    {
      path: '/records/:id',
      name: 'records',
      component: Records,
    },
    {
      path: '/journals',
      name: 'journals',
      component: Journals,
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
    },
    {
      path: '/register/verify',
      name: 'registerVerify',
      component: RegisterVerify,
    },
    {
      path: '/register/success',
      name: 'registerSuccess',
      component: RegisterSuccess,
    },
    {
      path: '/perfectposition',
      name: 'PerfectPosition',
      component: PerfectPosition,
    },
    {
      path: '/dataverify',
      name: 'DataVerify',
      component: DataVerify,
    },
  ],
});

router.beforeEach((to, from, next) => {
  const { title } = to.meta;
  if (title) {
    document.title = title;
  }
  if (to.path === '/login') {
    const loginToken = localStorage.getItem('token');
    if (loginToken) {
      AuthAPI.isLogin().then((response) => {
        if (response.status === 200) {
          response.json().then((data) => {
            const { token, userId, type, city } = data;
            if (token) {
              localStorage.setItem('token', token);
              localStorage.setItem('userId', userId);
              localStorage.setItem('userType', type);
              localStorage.setItem('city', city);
              next(from);
            }
          });
        } else {
          localStorage.removeItem('token');
          localStorage.removeItem('userId');
          localStorage.removeItem('userType');
          localStorage.removeItem('city');
          next();
        }
      });
    }
    next();
  }
  if (to.meta.auth) {
    const token = localStorage.getItem('token');

    if (!token && to.path !== '/login') {
      const { userType } = to.query;
      const params = {
        to: to.fullPath,
        userType,
      };
      next({ path: '/login', query: { ...params } });
    }
  }
  next();
});

export default router;



