<template>
  <div class="register">
    <mt-header fixed title="注 册" class="head">
      <router-link to="/" slot="left">
        <icon name="fanhui" scale="1.4" class="iconn"></icon>
      </router-link>
    </mt-header>
    <div class="register-box">
      <span class="left">手机号</span>
      <input type="tel" placeholder="请输入手机号码" v-model="tel.telephone">
      <div class="icon right" @click="deltel()" v-show="tel.delnum">
        <icon name="guanbi" class="iconn" scale='1.3'></icon>
      </div>
    </div>
    <router-link to="/register/verify">
      <mt-button type="primary" class="registernextStep">下一步</mt-button>
    </router-link>
    <span class="register-xieyi">点击 “下一步” 代表您已经同意
      <a href="javascript:;">《货权全服务协议》</a>
    </span>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tel: {
        telephone: '',
        delnum: false,
      },
    };
  },
  methods: {
    deltel() {
      this.tel.telephone = '';
    },
  },
  mounted() {
    const telinput = this.$el.querySelector('.register-box input');
    const next = this.$el.querySelector('.registernextStep');
    telinput.onkeyup = () => {
      if (telinput.value[0] === '1' && telinput.value.length >= 1) {
        next.style.background = '#ccac6f';
        next.removeAttribute('disabled');
        this.tel.delnum = true;
        if (telinput.value.length >= 11) {
          this.tel.telephone = telinput.value.substr(0, 11);
        }
      } else if (telinput.value.length === 0) {
        next.style.background = '#C0C0C0';
        this.tel.delnum = false;
      }
    };
  },
};
</script>
<style lang='scss'>
@import "../styles/index.scss";
.register {
  .register-box {
    margin-top: 1.2rem;
    height: 0.8rem;
    text-align: left;
    border-bottom: 1px solid $line-color;
    display: flex;
    background: #fff;
    font-size: 0.3rem;
    color: $none-color;
    .left {
      width: 20%;
      height: 100%;
      flex-grow: 0;
      text-indent: 0.33rem;
      line-height: 0.8rem;
      color: $font-color;
      font-weight: bold;
    }
    input {
      flex-grow: 1;
      border: none;
      outline: none;
      color: $font-color;
      letter-spacing: 1px;
    }
    .right {
      width: 1.08rem;
      height: 0.8rem;
      display: flex;
      @extend .vh-center;
    }
  }
  .registernextStep {
    @include nextStep;
    background: $main-color;
  }
  .register-xieyi {
    display: block;
    width: 100%;
    text-align: center;
    margin-top: 0.4rem;
    font-size: 0.2rem;
    color: $none-color;
    a {
      color: #CCAC6F;
    }
  }
}
</style>




