<template>
  <div class="message" v-show="mes.onoff">
    <div class="win">
      <div class="head">
        <span>{{mes.headmes}}</span>
        <div class="close" @click="closeWrap()" v-show='mes.state'>
          <icon name="guanbitanchuang" class="iconn"></icon>
        </div>
      </div>
      <img v-bind:src='mes.image' alt="">
      <div class="foot" @click="closeWrap()">{{mes.footmes}}</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'message',
  props: {
    mes: {
      onoff: true,
      headmes: '',
      state: false,
      footmes: '',
      image: '',
    },
  },
  methods: {
    closeWrap() {
      this.mes.onoff = !this.mes.onoff;
    },
  },
};
</script>
<style lang='scss'>
@import '../styles/index.scss';
.message {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  @extend .vh-center;
  .win {
    width: 80%;
    height: 36%;
    background: #fff;
    border-radius: 10px;
    position: relative;
    img {
      width: 50%;
      position: absolute;
      left: 50%;
      transform: translate(-50%, 1.2rem);
    }
    .head {
      width: 100%;
      height: 0.4rem;
      position: relative;
      text-align: center;
      line-height:1.3rem;
      font-size:0.34rem;
      letter-spacing: 2px;
      color: #CCAC6F;
      .close {
        width: 1rem;
        height: 1rem;
        position: absolute;
        right: 0;
        top: 0;
        color: #333;
        line-height: 1rem;
      }
    }
    .foot {
      @include nextStep(#CCAC6F);
      width: 60%;
      letter-spacing: 1px;
      position: absolute;
      left:50%;
      transform: translate(-50%);
      bottom:0.5rem;
    }
  }
}
</style>



