<template>
  <div class="goods">
    <div class="goods-avatar">
      <img :src="goods.thumbnail | img" :alt="goods.name">
    </div>
    <div class="goods-info">
      <div class="name-spec">
        <p class="name">{{goods.name}}</p>
        <p class="spec">{{goods.spec}}</p>
      </div>
      <div class="right">
        <icon name="right" scale="2"></icon>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    goods: Object,
  },
};
</script>

<style lang="scss">
@import '../styles/index.scss';

.goods {
  padding: $gutter;
  border: 1px solid #eee;
  border-radius: 8px;
  margin: $gutter 1rem 0 1rem;
  background-color: #fff;
  box-shadow: 2px 2px 4px #eee;
  @extend .vh-center;
  .goods-avatar {
    flex: 1;
    img {
      border: 1px solid #eee;
      max-width: 4rem;
    }
  }
  .goods-info {
    flex: 3;
    align-items: center;
    flex-direction: row;
    display: flex;
    justify-content: space-between;
    .name-spec {
      .name {
        font-size: 1rem;
        color: $font-color;
      }
      .spec {
        color: $active-theme-color;
      }
    }
    .right {
      color: $grey-color;
    }
  }
}
</style>




