<template>
  <div class="record">
    <mt-header fixed title="录入价格 / 记账">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="record-spec">
      <span>规格：{{goods.spec}} / {{goods.units}}</span>
    </div>
    <div class="record-goods">
      <div class="record-goods-avatar">
        <img :src="goods.thumbnail | img" :alt="goods.name">
      </div>
      <div class="record-goods-info">
        <div>
          <span class="record-goods-info-name">品&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;牌：</span>
          <span class="record-goods-info-value">{{goods.brand}}</span>
        </div>
        <div>
          <span class="record-goods-info-name">一级分类：</span>
          <span class="record-goods-info-value"></span>
        </div>
        <div>
          <span class="record-goods-info-name">二级分类：</span>
          <span class="record-goods-info-value"></span>
        </div>
        <div>
          <span class="record-goods-info-name">条&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;码：</span>
          <span class="record-goods-info-value">{{goods.barcode}}</span>
        </div>
      </div>
    </div>
    <div class="record-goods-name">
      <span>{{goods.name}}</span>
    </div>
    <div class="record-info">
      <mt-cell title="进货日期" @click.native="openPicker">
        <span>{{record.stockDate | date}}</span>
        <icon name="rili" scale="1.5"></icon>
      </mt-cell>
      <mt-field label="进货单价" placeholder="请输入单价" type="number" v-model="record.price"></mt-field>
      <mt-field label="进货数量" placeholder="请输入进货量" type="number" v-model="record.amount"></mt-field>
    </div>
    <div class="record-warning">
      <span>优惠信息</span>
    </div>
    <div class="record-discount">
      <mt-cell title="优惠形式">
        <span class="discount" :class="{'active': item.selected}" v-for="(item, index) in discount" :key="item.name" @click="selectDiscount(index)">{{item.title}}</span>
      </mt-cell>
      <mt-field label="优惠金额" placeholder="请输入优惠总金额" type="number" v-model="record.discount"></mt-field>
      <mt-field label="优惠数量" placeholder="请输入优惠总数量" type="number" v-model="record.quantity"></mt-field>
    </div>
    <div class="record-price">
      <div>
        <span><em>优惠后单价：</em><b>￥</b><i>{{discountPrice | price}}</i> 元</span>
        <span><em>合计总金额：</em><b>￥</b><i>{{total | price}}</i> 元</span>
      </div>
    </div>
    <div class="record-btn">
      <mt-button type="primary" size="large" @click.native="saveRecord">确认入库</mt-button>
    </div>
    <mt-datetime-picker ref="picker" type="date" v-model="record.stockDate">
    </mt-datetime-picker>
  </div>
</template>

<script>
import { MessageBox, Toast, Indicator } from 'mint-ui';
import { GoodsAPI, RecordAPI } from '@/api';

export default {
  data() {
    return {
      goods: {
        id: null,
      },
      record: {
        price: null,
        amount: null,
        discount: null,
        quantity: null,
        stockDate: new Date(),
      },
      discount: [
        {
          name: 'dz',
          title: '搭赠',
          selected: false,
        },
        {
          name: 'fl',
          title: '返利',
          selected: false,
        },
        {
          name: 'cl',
          title: '陈列',
          selected: false,
        },
        {
          name: 'dt',
          title: '堆头',
          selected: false,
        },
      ],
    };
  },
  mounted() {
    const { id } = this.$route.params;
    this.goods.id = id;
    this.getData();
  },
  computed: {
    discountPrice() {
      let total = this.record.price * this.record.amount;
      total -= this.record.discount;
      const fee = Number(this.record.amount) + Number(this.record.quantity);
      if (fee === 0) {
        return 0;
      }
      return total / fee;
    },
    total() {
      return this.record.price * this.record.amount;
    },
  },
  methods: {
    getData() {
      GoodsAPI.findById(this.goods.id).then((response) => {
        this.goods = response.data;
      });
    },
    openPicker() {
      this.$refs.picker.open();
    },
    selectDiscount(index) {
      const item = this.discount[index].selected;
      if (item) {
        this.discount[index].selected = false;
      } else {
        this.discount[index].selected = true;
      }
    },
    saveRecord() {
      MessageBox.confirm('确认提交吗？').then(() => {
        const data = {
          ...this.goods,
          ...this.record,
          id: null,
        };
        Indicator.open();
        RecordAPI.save(data).then(() => {
          Indicator.close();
          this.$router.replace({ name: 'records', params: { id: this.goods.id } });
        }).catch(() => {
          Toast({
            message: '出错了！',
            position: 'bottom',
            duration: 2000,
          });
          Indicator.close();
        });
      }, (action) => {
        console.log(action); // eslint-disable-line no-console
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import '../styles/index.scss';

.record {
  margin-top: $mt-header-height;
  .record-spec {
    @extend .v-center;
    padding: $gutter/2;
    background-color: $active-theme-color;
    color: #fff;
  }
  .record-goods {
    background-color: #fff;
    padding: $gutter/2 $gutter;
    display: flex;
    .record-goods-avatar {
      margin-right: $gutter;
      img {
        max-width: 6rem;
        border: 1px solid $grey-color;
        border-radius: 6px;
      }
    }
    .record-goods-info {
      .record-goods-info-name {
        color: #999;
      }
    }
  }
  .record-goods-name {
    margin-top: 1px;
    padding: $gutter;
    background-color: #fff;
    color: #333;
    font-weight: 500;
    font-size: 1rem;
  }
  .record-info {
    margin-top: $gutter;
    .mint-cell-value > span {
      color: #000;
    }
  }
  .record-warning {
    @extend .vh-center;
    padding: $gutter;
    span {
      padding: $gutter/2 $gutter;
      text-align: center;
      border-top-left-radius: 45px;
      border-top-right-radius: 45px;
      border-bottom-right-radius: 45px;
      border-bottom-left-radius: 45px;
      background-color: $active-color;
      color: #fff;
      font-size: $gutter;
    }
  }
  .record-discount {
    .discount {
      padding: $gutter/2 $gutter $gutter/2 $gutter;
      border: 1px solid #efefef;
      border-radius: 4px;
      margin-left: 8px;
      font-size: .875rem;
      &.active {
        border-color: $theme-color;
        color: #fff;
        background-color: $theme-color;
      }
    }
  }
  .record-price {
    margin-top: $gutter;
    background-color: #fff;
    @extend .vh-center;
    padding: $gutter;
    font-size: .875rem;
    & > div {
      @extend .v-center;
      flex-direction: column;
      span {
        em {
          font-style: normal;
        }
        i {
          font-style: normal;
          font-size: 1.25rem;
          color: $theme-color;
        }
        b {
          font-style: normal;
          color: $theme-color;
        }
      }
    }
  }
  .record-btn {
    padding: $gutter;
    margin-bottom: $gutter;
  }
  /* webkit solution */
   ::-webkit-input-placeholder {
    text-align: right;
  }
  /* mozilla solution */
  input:-moz-placeholder {
    text-align: right;
  }
  input {
    text-align: right;
    padding-right: $gutter/2;
  }
}
</style>



